export default {
  data() {
    return {
      //表单数据
      form_data: {
        // 手机
        user_tel: {
          value: "", //v-model绑定值
          left_icon: "s-phone", //左边图标
          type: "tel",
          placeholder: "输入手机号" //提示文字
        },
        //密码
        user_password: {
          value: "", //v-model绑定值
          left_icon: "s-password", //左边图标
          type: "password",
          placeholder: "请输入至少6位密码" //提示文字
        }
      }
    };
  },
  methods: {
    //性别选择
    sex_select(v) {
      this.form_data["user_sex"]["value"] = v["text"];
    },
    //构造数据
    reset_data() {
      let data = {
        tel: "",
        password: ""
      };
      data["tel"] = this.form_data["user_tel"]["value"];
      data["password"] = this.form_data["user_password"]["value"];
      return data;
    },
    //表单提交
    button_click() {
      console.log(this.reset_data());
    },
    //返回路由
    router_back() {
      this.$router.go(-1);
    },
    //跳转忘记密码
    router_nopassword() {
      this.$store.commit({
        type: "set_show_component",
        data: "忘记密码"
      });
    },
    //跳转注册
    router_register() {
      this.$store.commit({
        type: "set_show_component",
        data: "注册"
      });
    }
  }
};
